body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.App {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center
}

.page {
	background-color: whitesmoke;
	width: 80%;
	height: 500px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
	-webkit-align-items: center;
	        align-items: center;
	margin-top: 50px;
	background-image: url("https://previews.123rf.com/images/prapann/prapann1802/prapann180200055/94966218-rough-white-paper-texture-background.jpg")
}

.card {
	width: 300px;
	height: 400px;
}

.card .card-image {
	margin: 0;
	width: 100%;
}

.SimpleCard-left-5 div {
	padding: 0;
	margin: 0;
}

.card.right {}

.card.right>div {
	border-right: white 1px solid;
}

.left-side {
	grid-column: 1;
	padding: 10px !important;
}

.right-side {
	grid-column: 2/3;
	padding: 10px !important;
}

.player-box {
	position: relative;
}

.player-name {
	/* transition: v   isibility 0s linear 0s, opacity 400ms; */
	position: absolute;
	left: 0;
	top: 330px;
	background: rgba(0, 0, 0, 0.5);
	width: 100%;
	height: 75px;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	z-index: 2;
	display: -webkit-flex;
	display: flex;
	font-size: 30px;
	color: white;
}
.App-logo {
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
	height: 40vmin;
	pointer-events: none;
}

.App-header {
	/* background-color: #282c34; */
	min-height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}

	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}

	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
